body {
  margin: 0;
}

img {
  width: 150px;
  height: 150px;
}

.app-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: #00000050 url(../images/weatherbg.jpg);
  background-blend-mode: multiply;
}

.weatherForm {
  height: 400px;
  padding: 30px 30px 0;
  background: rgb(9, 74, 227);
}

input {
  font-size: 16px;
  height: 40px;
  border: none;
  outline: none;
  padding-left: 15px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
}
button {
  height: 41px;
  font-size: 16px;
  border: none;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  cursor: pointer;
}